import bg from "../../img/chooseUs/bg.png"
import main from "../../img/chooseUs/main.png"
import mark from "../../img/chooseUs/check-mark.png"

import "./choose.sass"

export default function Choose({setOpenForm}) {
    const chooseList = [
        {
            id: 1,
            title: "--",
            descr: "                .",
        },
        {
            id: 2,
            title: " ",
            descr: " ,   ,           .",
        },
        {
            id: 3,
            title: "  ",
            descr: "       .       .",
        }
    ]

  return (
    <section style={{ backgroundImage: `url(${bg})` }} className='choose-section'>
        <div className='container'>
            <div className='choose'>
                <img className='choose__img' src={main} alt=''/>
                <div className='choose__text'>
                    <div className='choose__left'>
                        <h4>    ?</h4>
                        <h2> ,   - </h2>
                        <p>    ,   - ,    .         ,        ,   .     ,       ,     .</p>
                        <button onClick={() => setOpenForm(true)}>  &nbsp;<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none" stroke="currentColor" strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" className="tabler-icon tabler-icon-chevron-right"><path d="M9 6l6 6l-6 6"></path></svg></button>
                    </div>
                    <div className='choose__right'>{
                        chooseList.map(e => (
                            <div key={e.id} className='choose__right__box'>
                                <img src={mark} alt=''/>
                                <div className='choose__right__text'>
                                    <h4>{e.title}</h4>
                                    <p>{e.descr}</p>
                                </div>
                            </div>  
                        ))
                    }</div>
                </div>
            </div>
        </div>
    </section>
  )
}
